.container-cars {
    margin-left: auto;
    margin-right: auto;
    max-width: $max-width;
    padding-left: 10px;
    padding-right: 10px;
    width: 100%;
}

.filter {
    background-color: #fff;
    border-radius: 6px;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, .25), 0 8px 16px -8px rgba(0, 0, 0, .3), 0 -6px 16px -6px rgba(0, 0, 0, .025);
    margin-bottom: 2.5rem;
    padding: 0;
    position: relative;
}

#voicesearch {
    display: none;
}

.show-filters {
    @include desktop {
        overflow: auto;
    }

    overflow: hidden;

    .listing-container {
        aside {
            display: block;
        }

        .characteristics {
            padding-bottom: 120px;
        }
    }

    .filters-footer {
        bottom: 0;
        display: block;
        position: fixed;
        width: 100%;

        button {
            background: $primary-color;
            border: 0;
            border-radius: 0;
            color: #fff;
            cursor: pointer;
            font-size: 13px;
            margin: 0;
            padding: 18px 0;
            text-align: center;
            width: 100%;
        }

        .ais-ClearRefinements-button {
            background: $header-secondary-color;
        }

        .block {
            float: left;
            font-size: 11px;
            padding: 0;
            width: 50%;
        }

    }
}

.filters-footer {
    @include desktop {
        display: none;
    }
}

.listing-container {
    @include desktop {
        display: flex;
        margin: 0 auto;
        max-width: $max-width;
        padding: 2rem 1rem;

        .ais-Hits-item {
            width: 46.5%;
        }
    }

    @include large {
        .ais-Hits-item {
            width: 31%;
        }
    }


    .ais-RangeSlider {
        padding: 0 40px;
    }

    .ais-RefinementList {
        margin-bottom: 10px;
        padding-left: 30px;
    }

    aside {
        @include desktop {
            display: block;
            flex: 1;
            font-size: 14px;
            height: auto;
            margin-left: 10px;
            margin-right: 20px;
            max-width: 260px;
            overflow-y: visible;
            position: relative;
            z-index: auto;

            label {
                font-weight: normal;
                cursor: pointer;
            }
        }

        background-color: $body-background;
        display: none;
        height: 100vh;
        overflow-y: scroll;
        padding: 0;
        position: fixed;
        top: 0;
        width: 100%;
        z-index: 100;

        .h3 {
            background: linear-gradient(90deg, $primary-color, $header-secondary-color);
            border-radius: 6px 6px 0 0;
            color: $white;
            display: block;
            font-size: 18px;
            margin: 0;
            padding: 6px 10px;
            width: 100%;
        }

        .toggle-content.collapsed {
            margin-top: 10px;
        }

        .toggle {
            line-height: 20px;

            &:after {
                border-bottom: 0;
                border-left: 0;
                border-radius: 0;
                border-color: $white;
                height: 14px;
                top: 8px;
                width: 14px;
            }
        }
    }

    .results-wrapper {
        @include desktop {
            flex: 3;
        }
    }

    .ais-RefinementList-checkbox {
        display: inline-block;
        position: relative;
    }


    .ais-HierarchicalMenu-count,
    .ais-Menu-count,
    .ais-RefinementList-count,
    .ais-ToggleRefinement-count {
        background-color: #dfe2ee;
        border-radius: 8px;
        color: #3a4570;
        font-size: .8rem;
        padding: .1rem .4rem;
    }

    .ais-Breadcrumb-list,
    .ais-CurrentRefinements-list,
    .ais-HierarchicalMenu-list,
    .ais-Hits-list,
    .ais-InfiniteHits-list,
    .ais-InfiniteResults-list,
    .ais-Menu-list,
    .ais-NumericMenu-list,
    .ais-Pagination-list,
    .ais-RatingMenu-list,
    .ais-RefinementList-list,
    .ais-Results-list,
    .ais-ToggleRefinement-list {
        list-style: none;
        margin: 0;
        padding: 0;
    }
}

.ais-SearchBox-form {
    .ais-SearchBox-reset {
        display: none;
    }

    .ais-SearchBox-submit {
        svg {
            fill: $white;
            height: 20px;
            top: 2px;
            width: 20px;
        }
    }
}

#clear-refinements-show-all {
    @include desktop {
        display: block;

    }

    display: none;

    button {
        background: #009d76;
        border-color: transparent;
    }

    .ais-ClearRefinements {
        text-align: center;
    }

}


.ais-RangeSlider {
    .rheostat {
        margin-bottom: 40px;
        margin-top: 40px;
        overflow: visible;
    }

    .rheostat-background {
        height: 6px;
        top: 0;
        width: 100%;
    }

    .rheostat-handle {
        margin-left: -12px;
        top: -7px;
    }

    .rheostat-handle-lower  {
        .rheostat-tooltip {
            top: 25px;
        }
    }

    .rheostat-background {
        position: relative;
        background-color: #fff;
        border: 1px solid #aaa;
    }

    .rheostat-progress {
        background-color: #333;
        height: 4px;
        position: absolute;
        top: 1px;
    }
}

.rheostat-handle {
    background-color: #fff;
    border: 1px solid #333;
    border-radius: 50%;
    cursor: -webkit-grab;
    cursor: grab;
    height: 20px;
    position: relative;
    width: 20px;
    z-index: 1;
}

.rheostat-marker {
    background-color: #aaa;
    height: 5px;
    margin-left: -1px;
    position: absolute;
    width: 1px;
}

.rheostat-marker--large {
    height: 9px;
}

.rheostat-value {
    display: none;
    padding-top: 15px;
}

.rheostat-tooltip,
.rheostat-value {
    margin-left: 50%;
    position: absolute;
    text-align: center;
    transform: translateX(-50%);
}

.rheostat-tooltip {
    font-size: 80%;
    top: -22px;
    width: 80px;
    background: $white;
}

.ais-Pagination-list {
    justify-content: center;
}

.ais-Pagination-item+.ais-Pagination-item {
    margin-left: .3rem;
}

.ais-Pagination-link {
    border: 1px solid #c4c8d8;
    border-radius: 5px;
    display: block;
    padding: .3rem .6rem;
    transition: background-color .2s ease-out;
}

.ais-Pagination-link:focus,
.ais-Pagination-link:hover {
    background-color: #e3e5ec;
}

.ais-Pagination-item--disabled .ais-Pagination-link {
    color: #a5abc4;
    cursor: not-allowed;
    opacity: .6;
}

/*
.ais-Pagination-item--disabled .ais-Pagination-link:focus,
.ais-Pagination-item--disabled .ais-Pagination-link:hover {
    color: #a5abc4;
    background-color: #fff;
}

.ais-Pagination-item--selected .ais-Pagination-link {
    background-color: #f25900;
    border-color: #f25900;
    color: #fff;
}

.ais-Pagination-item--selected .ais-Pagination-link:focus,
.ais-Pagination-item--selected .ais-Pagination-link:hover {
    color: #fff;
}

