.ais-RefinementList-showMore {
    background-color: $header-secondary-color;
    border: 0;
    border-radius: 5px;
    color: #fff;
    font-size: .8rem;
    margin: 0 0 .5rem;
    padding: .3rem .5rem;
    text-align: center;

    &:hover,
    &:focus {
        background: $primary-color;
        border: 0;
    }
}

.ais-Breadcrumb-item,
.ais-Breadcrumb-list,
.ais-Pagination-list,
.ais-PoweredBy,
.ais-RangeInput-form,
.ais-RatingMenu-link {
    align-items: center;
    display: flex;
}


.ais-Pagination-item {
    background-color: #fff;
    box-sizing: border-box;
    color: #111;
    display: block;
    float: left;
    font-size: 13px;
    line-height: 16px;
    list-style: none;
    margin: 0;
    margin-right: 4px;
    position: relative;
    word-wrap: break-word;

    a {
        background: linear-gradient(to bottom, #f7f8fa, #e8e9eb);
        border: 1px solid #6c6e73;
        border-color: #adb1b8 #a2a6ac #8d9096;
        border-radius: 3px;
        box-shadow: 0 1px 0 rgba(255, 255, 255, .5) inset;
        box-sizing: border-box;
        color: #111;
        display: block;
        font-size: 13px;
        line-height: 16px;
        list-style: none;
        padding: 8px 12px 7px 13px;
        text-decoration: none;

        &:hover,
        &:focus {
            background: linear-gradient(to bottom, #e7eaf0, #d9dce1);
            border-color: #a2a6ac #979aa1 #82858a;
            color: #111;
            text-decoration: none;
        }
    }
}


.ais-Pagination-item--selected,
.ais-Pagination-item--selected {
    background-color: #fff;
    border-color: #e77600;

    a,
    a:hover,
    &:focus {
        background-color: #fff;
        background-image: none;
        border-color: #e77600;
        color: #c45500;
        font-weight: 700;
    }
}



.ais-Pagination-item--disabled {
    display: none;
}

.ais-Pagination {
    padding-bottom: 30px;
}

#results-topbar {
    display: inline-block;
    padding: 10px 30px;
    width: 100%;

    .sort-by {
        float: right;
        max-width: 400px;
        width: 100%;
    }

    #stats {
        display: none;
    }
}

.ais-RefinementList-count {
    display: none;
}


