.ais-PoweredBy-text,.ais-Stats-text,.rheostat-tooltip,.rheostat-value {
    font-size: .8rem;
    width:80px;
}

.ais-PoweredBy-logo {
    margin-left: .3rem
}

.ais-RangeSlider .rheostat-progress {
    background-color: #495588
}

.ais-RangeSlider .rheostat-background {
    border-color: #878faf;
    box-sizing: border-box
}

.ais-RangeSlider .rheostat-handle {
    border-color: #878faf
}

.ais-RangeSlider .rheostat-marker {
    background-color: #878faf
}

.ais-Panel-body .ais-RangeSlider {
    margin: 2rem 0
}

.ais-RangeSlider-handle {
    width: 20px;
    height: 20px;
    position: relative;
    z-index: 1;
    background: #fff;
    border: 1px solid #46aeda;
    border-radius: 50%;
    cursor: pointer
}

.ais-RangeSlider-tooltip {
    position: absolute;
    background: #fff;
    top: -22px;
    font-size: .8em
}

.ais-RangeSlider-value {
    width: 40px;
    position: absolute;
    text-align: center;
    margin-left: -20px;
    padding-top: 15px;
    font-size: .8em
}

.ais-RangeSlider-marker {
    position: absolute;
    background: #ddd;
    margin-left: -1px;
    width: 1px;
    height: 5px
}

.ais-RatingMenu-item--disabled .ais-RatingMenu-count,.ais-RatingMenu-item--disabled .ais-RatingMenu-label {
    color: #c4c8d8
}

.ais-RatingMenu-item--selected {
    font-weight: 700
}

.ais-RatingMenu-link {
    line-height: 1.5
}

.ais-RatingMenu-link>*+* {
    margin-left: .3rem
}

.ais-RatingMenu-starIcon {
    position: relative;
    top: -1px;
    width: 15px;
    fill: #ffc168
}

.ais-RatingMenu-item--disabled .ais-RatingMenu-starIcon {
    fill: #c4c8d8
}

.ais-HierarchicalMenu-searchBox>*,.ais-Menu-searchBox>*,.ais-RefinementList-searchBox>* {
    margin-bottom: .5rem
}

.ais-SearchBox-form {
    display: block;
    position: relative
}

.ais-SearchBox-input {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    padding: .3rem 1.7rem;
    width: 100%;
    position: relative;
    background-color: #fff;
    border: 1px solid #c4c8d8;
    border-radius: 5px
}

.ais-SearchBox-input::-webkit-input-placeholder {
    color: #a5aed1
}

.ais-SearchBox-input::-moz-placeholder {
    color: #a5aed1
}

.ais-SearchBox-input:-ms-input-placeholder {
    color: #a5aed1
}

.ais-SearchBox-input:-moz-placeholder {
    color: #a5aed1
}
